import { ADD_PERSON, DELETE_PERSON, FILTER, ORDER } from "./types";

const initialState = {
    myFavorites: [],
    allCharacters: [],
    errors:{}
}
// {type,payload}
export default function reducer(state = initialState, { type, payload }) {

    switch (type) {

        case ADD_PERSON: {
console.log("add_person", type,"payload",payload)
            return {
                ...state,
                myFavorites: payload,
                allCharacters: payload,
                errors:{}
                //myFavorites: [...state.myFavorites, payload]
            }
        }
        case DELETE_PERSON: {
            // console.log("delete",state.myFavorites.id);
            // const filtered = state.myFavorites.filter(per =>per.id !== payload)
            // return {
            //     ...state,
            //     myFavorites:filtered

            // }
            console.log("add_person", type,"payload",payload)
            return {
                ...state,
                myFavorites: payload,
                errors:{}

            }
        }
        case FILTER: {
            
            const filtercopy = [...state.allCharacters]
            const filtered = filtercopy.filter(per =>{
                console.log("persona",per.gender,"payload",payload)
               return per.gender === payload
            })
            console.log("filtrado",filtercopy)
            return {
                ...state,
                myFavorites: filtered
            }
        }
        case ORDER: {
            const ordercopy = [...state.allCharacters]
            const orderfilter = ordercopy.sort((a,b) => {
                if(a.id < b.id){
                    if(payload === "Ascendente"){
                        return 1
                    }else{
                        return -1
                    }
                }
                if(a.id > b.id){
                    if(payload === "Descendente"){
                        return -1
                    }else{
                        return 1
                    }
                }else{
                    return 0
                }
            })
            return {
                ...state,
                myFavorites: orderfilter
            }
        }
        case "ERROR":{

            return({
                ...state,
                errors:payload
            })
        }
        case "GET_FAVOTITES":{
            return({
                ...state,
                myFavorites:payload,
                errors:{}
            })
        }

        default: {
            return { ...state }
        }
    }
};
